import React from "react";
import "./project-card.css";
function ProjectCard({ project }) {
  return (
    <div className="project-card">
      <div className="project-info">
        <label className="project-title">{project.title}</label>
        <div className="project-links">
          {project.demo && (
            <a className="project-link" href={project.demo} lang="de">
              <div className="link-button">
                <i class="fi-rr-globe"></i>&nbsp;{project.linktext}
              </div>
            </a>
            )}
            {project.github&& (
              <a className="project-link" href={project.github}>
                <div className="link-button">
                <i class="devicon-github-original colored"></i>Github
              </div>
            </a>
            )}
        </div>
        <div>
          <label className="project-subtitle">Goal:</label> {project.goal}
          </div>
        <div><label className="project-subtitle">Role:</label> {project.role}
        </div>
        <p>{project.about}</p>
        <p>{project.about2}</p>
        <div className="project-tags">
          {project.tags.map((tag)=> {
            return <label className="tag">{tag}</label>;
          })}
        </div>
      </div>
      <img src={project.image} className="project-photo" />
    </div>
  );
}

export default ProjectCard;